import React, { } from "react";
import { WebNav } from "../../components/navbar";
import { Cover } from "../../components/cover";
import { Footer } from '../../components/footer';
import { MapContainer, TileLayer, Marker, Popup } from 'react-leaflet'
import { Container, Row, Col } from "react-bootstrap";


//
function Contact(params) {

  return (
    <React.StrictMode>
      <WebNav />
      <Cover pageName={{ tw: '', en: 'Contact' }} />
      <div className='contact'>
        <Position />
      </div>
      <Footer />
    </React.StrictMode>
  )
}

export { Contact };

function Position(props) {
  return (
    <Container>
      <Row>
        <Col xs={12} md={12}>
          <MapSection position={[22.996993, 120.223597]} />
        </Col>
      </Row>
      <Row className='mt-md-5 mt-5'>
        <Col xs={12} md={6}>
          <div className='title'>
            <p></p>
          </div>
        </Col>
        <Col xs={12} md={6}>
          <div className='text'>
            <p>7011</p>
            <p>No. 1, Daxue Rd., East Dist., Tainan City 701, Taiwan (R.O.C.)</p>
          </div>
        </Col>
      </Row>
      <hr />
      <Row>
        <Col xs={12} md={6}>
          <div className='title'>
            <p className='mb-0'></p>
            <p className='mb-0'>Office</p>
          </div>
        </Col>
        <Col xs={12} md={6}>
          <div className='text'>
            <p className='subtitle'></p>
            <p> 5F 91515</p>
            <p className='subtitle'></p>
            <p>(06)275-7575 ext 62171</p>
          </div>
        </Col>
      </Row>
      <hr />
      <Row>
        <Col xs={12} md={6}>
          <div className='title'>
            <p className='mb-0'></p>
            <p className='mb-0'>Laboratory</p>
          </div>
        </Col>
        <Col xs={12} md={6}>
          <div className='text'>
            <p className='subtitle'></p>
            <p> 12F 91C05</p>
            <p className='subtitle'></p>
            <p>(06)275-7575 ext 62159-33</p>
          </div>
        </Col>
      </Row>
    </Container>
  )
}

function MapSection(props) {
  var center = [props.position[0], props.position[1] - 0.005]

  return (
    <div style={{ position: 'relative', zIndex: '0' }}>
      <MapContainer center={center} zoom={15} scrollWheelZoom={false} style={{ height: '50vh' }}>
        <TileLayer
          attribution='&copy; <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
          url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
        />
        <Marker position={props.position}>
          <Popup>
            <br />NCKU.ME
      </Popup>
        </Marker>
      </MapContainer>
    </div>


  )
}
